<template>
  <thead class="sticky top-spacing-0 w-full" :class="bgClass">
    <tr
      class="text-body/m/relaxed/500 text-content-on-neutral-secondary nexus:text-body/l/relaxed/500"
    >
      <th
        v-for="header in headers"
        :key="header?.key"
        :class="[
          'min-w-spacing-400 gap-spacing-50 whitespace-nowrap py-spacing-100 pr-spacing-100 text-left text-current ipad:min-w-spacing-1000',
          header?.sortable ? 'cursor-pointer' : '',
          headerClass,
        ]"
        :title="header?.description"
        @click="header?.sortable ? handleSort(header?.key) : null"
      >
        {{ header.value }}
        <template v-if="header?.sortable">
          <IconArrowDown
            v-if="sortKey === header?.key && sortOrder === 'desc'"
            class="inline-block h-spacing-200 w-spacing-200"
            :class="
              sortKey === header?.key && sortOrder === 'desc'
                ? 'text-constant-brand-primary'
                : 'text-content-on-neutral-secondary'
            "
          />
          <IconArrowUp
            v-else
            :class="
              sortKey === header?.key
                ? 'text-constant-brand-primary'
                : 'text-content-on-neutral-secondary'
            "
            class="inline-block h-spacing-200 w-spacing-200"
          />
        </template>
      </th>
    </tr>
  </thead>
</template>

<script>
export default {
  components: {
    IconArrowDown: () => import("@/components/svgIcons/arrow-down.vue"),
    IconArrowUp: () => import("@/components/svgIcons/arrow-up.vue"),
  },
  props: {
    headers: {
      type: Array,
      required: true,
    },
    sortKey: {
      type: String,
      required: false,
      default: "",
    },
    sortOrder: {
      type: String,
      required: false,
      default: "",
    },
    bgClass: {
      type: String,
      required: false,
      default: "bg-bg-neutral-default",
    },
    headerClass: {
      type: String,
      required: false,
      default: "",
    },
  },
  methods: {
    handleSort(key) {
      let options = {
        sortKey: "",
        sortOrder: "",
      };
      options.sortKey = key;
      if (this.sortKey === key) {
        options.sortOrder = this.sortOrder === "asc" ? "desc" : "asc";
      } else {
        options.sortOrder = "desc";
      }
      this.$emit("fetch-sorted-data", options);
    },
  },
};
</script>
